@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
body
{
    font-family: 'Roboto', sans-serif;
}
.navigation
{
    display:flex;
    align-items: center;
    justify-content:flex-start;
    flex-direction: row;

    padding:20px;
   


    overflow-x: scroll;
   
   

}
.navigation li{
    list-style: none;
    text-decoration: none;
    

}
.navigation a{
    text-decoration:none;
    padding:15px;
    border-bottom: 5px solid #fff;
    color: black;
    transition:border 1s;
   



}
.navigation a:hover
{
    text-decoration:none;
    padding:15px;
    border-bottom: 5px solid black;
    color: black;
    
}
.navigation .active a
{
    text-decoration:none;
    padding:15px;
    border-bottom: 5px solid black ;
    color: black;
  
}
